.loginBlock {
  display: flex;
  justify-content: center;
  background-color: rgba(233, 233, 233, 0.363);
  margin: 0 auto;
  width: 50%;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 5px 5px 5px rgb(199, 199, 199);
}
.loginBlock > form > button.formBtn {
  margin-top: 20px;
  margin-right: 20px;
}
.formError {
  color: rgb(211, 4, 4);
  font-weight: bold;
}
@media (max-width: 375px) {
  .loginBlock {
    width: 90%;
  }
}
