.errorBlock {
  display: inline-flex;
  grid-area: err;
  justify-content: center;
  color: rgb(211, 4, 4);
  font-weight: 700;
  font-size: 0.8em;
  padding: 0 20px;
  width: fit-content;
  margin-left: 10px;
}
.activeField,
.formItem {
  display: grid;
  grid-template-areas: " lab inp err";
  grid-template-columns: 0.7fr 1fr 1fr;
  margin-bottom: 5px;
}
.activeField {
  background-color: rgba(141, 171, 177, 0.37);
  padding: 5px;
  height: 20px;
  border-radius: 8px;
  box-sizing: border-box;
}
.label {
  grid-area: lab;
  text-transform: capitalize;
}
.input {
  grid-area: inp;
}
textarea {
  height: 100%;
}
@media (max-width: 375px) {
  .activeField,
  .formItem {
    display: grid;
    grid-template-rows: 20px 30px;
    grid-template-areas:
      "lab err"
      "inp inp";
    grid-template-columns: 158px 237px;
    text-align: center;
  }

  .activeField {
    height: 60px;
    padding: 0 5px;
  }
}
