.contactItem,
.contactItemHover {
  display: block;
  position: relative;
  left: -3px;
  text-align: center;
  height: 40px;
  width: 80px;
  opacity: 0.3;
  transition: 0.3s;
  padding: 3px;
  box-sizing: border-box;
}
.contactItem > img,
.contactItemHover > img {
  position: relative;
  top: 2px;
  left: 15px;
  width: 25px;
  height: 25px;
  transition: 0.4s;
  border-radius: 10px;
}

.contactItemHover {
  border-radius: 0 10px 10px 0;
  opacity: 1;
  background-color: rgb(255, 221, 108);
  box-shadow: 2px 2px 2px rgb(199, 199, 199);
}
.contactItemHover > img {
  width: 32px;
  height: 32px;
}
@media (max-width: 375px) {
  .contactItem {
    display: inline-flex;
    justify-content: center;
    opacity: 0.8;
    padding: 0;
    width: 49px;
  }
  .contactItem > img {
    width: 35px;
    height: 35px;
    left: 35px;
    margin-top: 10px;
  }
}
