.profileContainer {
  display: grid;
  grid-template-areas:
    "h1 h1"
    "a b";
  grid-template-columns: 1.5fr 1fr;
}
.leftSide,
.rightSide {
  padding: 20px;
}
.leftSide {
  grid-area: a;
}
.rightSide {
  grid-area: b;
}
h1 {
  grid-area: h1;
}
@media (max-width: 375px) {
  .profileContainer {
    grid-template-areas:
      "h1"
      "a"
      "b";
    grid-template-columns: 1fr;
  }
  .rightSide {
    width: 400px;
  }
  .leftSide,
  .rightSide {
    padding: 0px;
  }
}
